* {
	font-family: 'Roboto', sans-serif;
	padding: 0;
	margin: 0; }
nav {
	position: fixed;
	background-color: black;
	min-height: 50px; }
header {
	.menu {
		display: flex;
		justify-content: space-between;
		align-items: center; } }
.menu {
	list-style-type: none;
	padding-left: 70px;
	padding-right: 71px;
	padding-top: 13px;
	padding-bottom: 13px;
	.menu_link {
		color: #FFF;
		font-size: 15px;
		font-style: normal;
		font-weight: 300; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }
.lowerHeader {
	background-color: rgba(0, 0, 0, 0.6);
	min-height: 86px;
	position: absolute;
	width: 100%;
	.subheader_official {
		color: #FFF;
		opacity: 0.5;
		font-size: 13px;
		font-style: normal;
		font-weight: 400;
		margin-top: 6px; }
	.subheader_logo {
		max-width: 100%;
		margin-top: 16px;
		display: block; }
	.calls {
		color: #FFF;
		text-align: right;
		margin-top: 16px;
		margin-right: 28px;
		font-size: 12px;
		font-style: normal;
		font-weight: 300;
		margin-top: 23px;
		color: #FFF;
		text-align: right;
		font-size: 12px;
		font-style: normal;
		font-weight: 300;
		line-height: 12px; }
	.link_calls {
		color: #FFF;
		text-align: center;
		font-size: 22px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase; }
	.order {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		border-radius: 6px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase; } }
.promo {
	min-height: 799px;
	background: url(../img/uber_bg.jpg) center center/cover no-repeat;
	.promo_header {
		margin-top: 67px;
		color: #FFF;
		text-align: center;
		font-size: 60px;
		font-style: normal;
		font-weight: 700;
		text-transform: uppercase; }
	.subHeader {
		margin-top: 22px;
		color: #FFF;
		font-family: Roboto;
		font-size: 36px;
		font-style: normal;
		font-weight: 700;
		text-align: center;
		text-transform: uppercase; }
	.descr {
		margin-top: 16px;
		color: #FFF;
		text-align: center;
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		line-height: 24px; }
	.promo_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		border-radius: 4px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
		color: #FFF;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		font-size: 18px;
		font-style: normal;
		font-weight: 400;
		text-transform: uppercase; } }
.Main {
	position: relative; }
.reasonz {
	min-height: 848px;
	margin-top: 20px;
	.subdescr {
		color: #222;
		text-align: center;
		font-size: 38px;
		font-style: normal;
		font-weight: 700;
		text-transform: uppercase; }
	.onerect {
		display: inline-block;
		width: 80px;
		height: 66px; }
	.reasons1descr {}
	.descr {
		color: #1EACC7;
		font-size: 17px;
		font-style: normal;
		font-weight: 400;
		line-height: 21px; }
	.reasons1subdescr {
		color: #202020;
		font-size: 15px;
		font-style: normal;
		font-weight: 300;
		line-height: 21px; }
	.circle {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 116px;
		width: 116px;
		background-color: #1EACC7;
		border-radius: 50%; }
	.Image1 {
		float: left; } }
.mobile {
	margin-top: 67px;
	min-height: 864px;
	background: url("../img/mobile_bg.jpg")center center/cover no-repeat;
	&_item {
		width: 320px;
		height: 240px;
		background: url(); } }
